.mainBossContainer{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
}

.bossSettingsContainer{
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}

.bossImgContainer{
    height: 255px;
    width: 150px;
    background-color: #FF3F3F;
    border-radius: 20px;
    margin-top: 40px;
    margin-bottom: 20px;
    margin: 40px 20px 20px;
}

.bossImg{
    position: absolute;
    height: 30%;
    width: 30%;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -75%);
}